/* SORTING_BOX */
/* SORTING_BOX */

.sorting-option  {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.sorting-option .status-option:active {
  background-color: rgb(3, 153, 3);;
}

