@media only screen and (max-width:550px) {
  .task-tracker-container .task-tracker-heading {
    font-size: 2rem;
    font-weight: 900;
  }
  
  
.new-task-form .input-box label {
  font-family: sans-serif;
  font-size: 17px;
  /* width: 33%; */
}

.new-task-form {
  width: 97%;
  min-width: 340px;
}
  
}

@media only screen and (max-width:440px) {
  
.new-task-form .input-box label {
  display: none;
}
.tasks-container-statuswise {
  min-width: 350px;
}
  
}

@media only screen and (max-width:360px) {

.tasks-container-statuswise {
  min-width: 330px;
}
  
}